.main {
  position: relative;
  padding: 60px 0;
  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 750px;
    background: #6492F4;
    border-radius: 40px;
    pointer-events: none;
    overflow: hidden;
    span:nth-child(1) {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 508px;
      img {
        width: 100%;
      }
    }
    span:nth-child(2) {
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 398px;
      img {
        width: 100%;
      }
    }
  }
  .container {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
    font-size: 84px;
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
  .search-form {
    margin-top: 60px;
    &__btn {
      padding: 19px 38px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    background: #F3F4F8;
    border-radius: 32px;
    padding: 32px;
    margin-top: 60px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 32px;
    background: #fff;
    border-radius: 32px;
    padding: 16px 32px;
    &-img {
      position: relative;
      max-width: 180px;
      width: 100%;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
      img:nth-child(2) {
        position: absolute;
        top: 0;
        right: 32px;
        max-width: 45px;
      }
    }
    .mob-img {
      display: none;
    }
    &-title {
      font-size: 24px;
      line-height: 130%;
      font-weight: 700;
      color: #407BFF;
    }
    .mob-title {
      display: none;
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      margin-top: 18px;
    }
    &-btn {
      padding: 8px 45px;
      margin-top: 32px;
    }
  }
}
.works {
  padding: 60px 0 80px;
  &__title {
    margin-bottom: 32px;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 32px;
  }
  &__item {
    display: flex;
    gap: 16px;
    &-number {
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #5A93ED;
      border-radius: 50%;
      font-size: 24px;
      line-height: 130%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      margin-top: 8px;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 32px;
  }
  &__item2 {
    display: flex;
    align-items: center;
    gap: 24px;
    background: #F3F4F8;
    border-radius: 24px;
    padding: 24px;
    &-img {
      max-width: 100px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
  }
}
.contests {
  background: #F3F4F8;
  border-radius: 32px 32px 0 0;
  padding: 64px 0 108px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__btn {
    padding: 8px 34px;
  }
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-pagination {
    bottom: -32px;
  }
}
.contest-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #CED4DA;
  border-radius: 40px;
  padding: 24px;
  &__title {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__text {
    margin-top: 24px;
    margin-bottom: auto;
  }
  &__term {
    margin-top: 24px;
    strong {
      display: block;
      margin-bottom: 5px;
    }
  }
}
.vacancies {
  background: #457EEF;
  border-radius: 32px;
  padding: 64px 0 108px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__title {
    color: #Fff;
  }
  &__btn {
    padding: 8px 34px;
  }
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev {
    left: -56px;
  }
  .swiper-button-next {
    right: -56px;
  }
  .swiper-pagination {
    bottom: -32px;
    &-bullet {
      border-color: #fff;
      &-active {
        background: #fff;
      }
    }
  }
}
.vacancy-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #CED4DA;
  border-radius: 40px;
  padding: 24px 20px;
  &__title {
    min-height: 62px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__text {
    margin-top: 24px;
    margin-bottom: auto;
  }
  &__salary {
    margin-top: 24px;
  }
}
.questions {
  padding: 80px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__btn {
    padding: 8px 34px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 40px;
  }
  &__item {
    background: #F3F4F8;
    border-radius: 40px;
    padding: 24px;
    &--active {
      .questions__item-btn {
        img {
          transform: rotate(-90deg);
        }
      }
    }
    &-show {
      display: flex;
      justify-content: space-between;
      gap: 8px;
      cursor: pointer;
    }
    &-title {
      margin-top: 3px;
    }
    &-btn {
      width: 26px;
      flex-shrink: 0;
      img {
        width: 100%;
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      padding: 24px 16px;
    }
  }
}
.connection {
  position: relative;
  z-index: 1;
  background: #6492F4;
  border-radius: 40px;
  padding: 64px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 58px;
  }
  &__left {
    width: 272px;
    .connection__title {
      display: none;
    }
  }
  &__right {
    width: calc(100% - 330px);
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__title {
    color: #fff;
  }
  .form {
    display: grid;
    grid-template-columns: 1.1fr 0.9fr;
    gap: 24px;
    margin-top: 32px;
    &-group {
      position: relative;
      input {
        width: 100%;
        height: 72px;
        background: #fff;
        border-color: transparent;
        border-radius: 40px;
        padding: 0 24px;
        font-size: 16px;
        line-height: 130%;
        &::placeholder {
          color: #ABABAB;
        }
      }
    }
    .file-input {
      input {
        display: none;
      }
      label {
        width: 100%;
        height: 72px;
        display: flex;
        align-items: center;
        background: #fff;
        border-color: transparent;
        border-radius: 40px;
        padding: 0 24px;
        font-size: 16px;
        line-height: 130%;
        color: #ABABAB;
      }
    }
    &-flex {
      grid-column: 1/3;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
    }
    &-check {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        padding: 0;
        margin: 0;
      }
      input:checked ~ label:after {
        background-image: url("../img/check.svg");
      }
      label {
        position: relative;
        display: block;
        color: #fff;
        padding-left: 40px;
        &:after {
          content: '';
          position: absolute;
          top: -4px;
          left: 0;
          width: 24px;
          height: 24px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          background-color: #fff;
          border-radius: 4px;
          transition: all 0.3s;
        }
      }
    }
    &-btn {
      padding: 8px 54px;
    }
  }
}

@media screen and (max-width: 1450px) {
  .vacancies {
    &__slider {
      padding: 0 56px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__title {
      font-size: 64px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__item {
      gap: 24px;
      &-img {
        max-width: 160px;
        img:nth-child(2) {
          right: 16px;
          max-width: 32px;
        }
      }
    }
  }
  .connection {
    .form {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 32px 0;
    &-bg {
      height: 530px;
      border-radius: 16px;
      span:nth-child(1) {
        max-width: 240px;
      }
      span:nth-child(2) {
        max-width: 160px;
      }
    }
    &__title {
      font-size: 46px;
    }
    .search-form {
      margin-top: 32px;
      &__btn {
        padding: 4px 24px;
      }
    }
    &__items {
      border-radius: 16px;
      padding: 24px;
      margin-top: 32px;
    }
    &__item {
      border-radius: 16px;
      padding: 16px;
      &-img {
        max-width: 95px;
        display: none;
        img:nth-child(2) {
          right: 0;
          max-width: 22px;
        }
      }
      .mob-img {
        display: block;
      }
      &-title {
        font-size: 16px;
      }
      &-text {
        margin-top: 8px;
      }
      &-btn {
        margin-top: 16px;
      }
    }
  }
  .works {
    padding: 32px 0 64px;
    &__group {
      &:not(:first-child) {
        margin-top: 24px;
      }
    }
    &__item2 {
      flex-direction: column;
      align-items: start;
      padding: 20px 16px;
      &-img {
        margin: 0 auto;
      }
    }
  }
  .contests {
    padding: 32px 0 108px;
    &__slider {
      margin-top: 24px;
    }
  }
  .vacancies {
    padding: 32px 0 68px;
    &__slider {
      padding: 0;
      margin-top: 24px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .questions {
    padding: 64px 0;
    &__items {
      margin-top: 32px;
    }
    &__item {
      padding: 16px;
      &-title {
        margin-top: 0;
      }
      &-btn {
        width: 24px;
      }
      &-hidden {
        padding: 16px;
      }
    }
  }
  .connection {
    border-radius: 32px;
    padding: 32px 0;
    &__inner {
      gap: 24px;
    }
    &__left {
      width: 168px;
    }
    &__right {
      width: calc(100% - 192px);
    }
    .form {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 24px;
      &-group {
        input {
          height: 56px;
        }
      }
      .file-input {
        label {
          height: 56px;
        }
      }
      &-flex {
        width: calc(100% + 192px);
        grid-column: unset;
        margin-left: -192px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .main {
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding: 16px 0 12px;
    &-bg {
      height: 800px;
      span:nth-child(1) {
        width: 170px;
      }
      span:nth-child(2) {
        width: 130px;
      }
    }
    &__title {
      font-size: 24px;
    }
    .search-form {
      margin-top: 16px;
      &__btn {
        max-width: 130px;
        padding: 4px 8px;
      }
    }
    &__items {
      gap: 16px;
      padding: 16px;
      margin-top: 16px;
    }
    &__item {
      &-btn {
        padding: 8px 39px;
      }
    }
  }
  .works {
    padding: 16px 0 32px;
    &__title {
      margin-bottom: 24px;
    }
    &__group {
      &:not(:first-child) {
        margin-top: 24px;
      }
    }
    &__items {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 16px;
    }
    &__item {
      gap: 16px;
    }
    &__items2 {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 16px;
    }
  }
  .contests {
    position: relative;
    border-radius: 24px 24px 0 0;
    padding: 24px 0 132px;
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translate(-50%, 0);
      white-space: nowrap;
    }
  }
  .vacancies {
    position: relative;
    border-radius: 24px;
    padding: 24px 0 132px;
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translate(-50%, 0);
      white-space: nowrap;
    }
  }
  .vacancy-card {
    &__salary {
     font-size: 24px;
    }
  }
  .questions {
    position: relative;
    padding: 32px 0 100px;
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translate(-50%, 0);
      white-space: nowrap;
    }
    &__items {
      gap: 12px;
      margin-top: 24px;
    }
    &__item {
      border-radius: 24px;
      &-btn {
        width: 16px;
      }
    }
  }
  .connection {
    border-radius: 24px;
    padding: 24px 0;
    &__inner {
      flex-direction: column;
      gap: 16px;
    }
    &__left {
      width: 100%;
      .connection__title {
        display: block;
      }
    }
    &__right {
      width: 100%;
    }
    &__img {
      max-width: 140px;
      margin: 16px auto 0;
    }
    &__title {
      display: none;
    }
    .form {
      margin-top: 0;
      &-group {
        input {
          height: 40px;
          padding: 0 16px;
        }
      }
      .file-input {
        label {
          height: 40px;
          padding: 0 16px;
        }
      }
      &-flex {
        width: 100%;
        flex-direction: column;
        gap: 24px;
        margin-left: 0;
      }
      &-check {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__item {
      flex-direction: column;
      align-items: start;
      gap: 16px;
      &-img {
        margin: 0 auto;
      }
      &-title {
        display: none;
      }
      .mob-title {
        display: block;
      }
    }
  }
}