.footer {
  background: #25345F;
  color: #fff;
  padding: 100px 0 64px 0;
  margin-top: -36px;
  .container {
    max-width: 1888px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 60px;
  }
  &__left {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 18px;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
  }
  &__logo {
    width: 55px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    border-left: 1px solid #565684;
    padding-left: 24px;
    li a {
      font-size: 16px;
      line-height: 130%;
      &:hover {
        color: #6492f4;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__inner {
      gap: 16px;
    }
    &__right {
      gap: 16px;
    }
    &__text {
      font-size: 14px;
    }
    ul {
      gap: 16px;
      li a {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      flex-direction: column;
      gap: 56px;
    }
    &__right {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 56px / 2));
      gap: 56px;
    }
    &__text {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 90px 0 32px;
    &__inner {
      gap: 48px;
    }
    &__left {
      gap: 8px;
    }
    &__right {
      grid-template-columns: 100%;
      gap: 32px;
    }
    &__logo {
      width: 30px;
    }
    &__text {
      font-size: 12px;
    }
  }
}