.header {
  position: relative;
  z-index: 5;
  background: #fff;
  padding: 26px 0;
  .container {
    max-width: 1888px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 18px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__logo {
    width: 55px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
  }
  ul {
    display: flex;
    align-items: center;
    gap: 40px;
    li {
      position: relative;
    }
    li a {
      font-size: 16px;
      line-height: 130%;
      &:hover {
        font-weight: 600;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__btn {
    background: transparent;
    border: 1px solid #000000;
    border-radius: 40px;
    padding: 8px 16px;
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      background: #457EEF;
      border-color: #457EEF;
      color: #fff;
    }
  }
  .login {
    background: transparent;
    padding: 0;
    font-size: 16px;
    line-height: 130%;
    &:hover {
      color: #457EEF;
    }
  }
  .submenu {
    &--active {
      .submenu-show {
        font-weight: 600;
      }
      .submenu-hidden {
        transform: translate(0, 0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-show {
      font-size: 16px;
      line-height: 130%;
      cursor: pointer;
      &:hover {
        font-weight: 600;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: -16px;
      transform: translate(0, -12px);
      width: 294px;
      padding-top: 12px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      background: #fff;
      border: 1px solid #CED4DA;
      border-radius: 16px;
      padding: 16px 0;
      a {
        font-size: 16px;
        line-height: 120%;
        font-weight: 300;
        padding: 8px 16px;
        &:hover {
          font-weight: 600;
          background: #F3F4F8;
          color: #000;
        }
      }
    }
  }
  .close {
    display: none;
  }
}
.burger {
  position: relative;
  width: 56px;
  height: 56px;
  display: none;
  background: #F3F4F8;
  border-radius: 50%;
  cursor: pointer;
  span {
    position: absolute;
    right: 12px;
    height: 1px;
    background: #000000;
  }
  span:nth-child(1) {
    top: 20px;
    width: 30px;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
    width: 20px;
  }
  span:nth-child(3) {
    bottom: 20px;
    width: 10px;
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__inner {
      gap: 24px;
    }
    &__right {
      gap: 24px;
    }
    ul {
      gap: 24px;
    }
    &__btns {
      gap: 24px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__inner {
      gap: 16px;
    }
    &__text {
      font-size: 14px;
    }
    &__right {
      gap: 16px;
    }
    ul {
      gap: 16px;
    }
    &__btns {
      gap: 16px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    position: relative;
    &__right {
      position: absolute;
      top: calc(100% - 30px);
      right: 12px;
      transform: translate(0, -12px);
      max-width: 300px;
      width: 100%;
      flex-direction: column;
      align-items: start;
      gap: 0;
      background: #fff;
      border: 1px solid #DEE2E6;
      border-radius: 16px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        transform: translate(0, 0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__text {
      font-size: 16px;
    }
    ul {
      width: 100%;
      flex-direction: column;
      align-items: start;
      gap: 0;
      li {
        width: 100%;
      }
      li a {
        width: 100%;
        display: block;
        padding: 8px 16px;
        font-weight: 400;
        &:hover {
          font-weight: 600;
          background: #F3F4F8;
          color: #000000;
          font-weight: 600;
        }
      }
    }
    &__btns {
      width: 100%;
      flex-direction: column;
      align-items: start;
      gap: 0;
    }
    &__btn {
      width: 100%;
      justify-content: start !important;
      align-items: start !important;
      background: transparent;
      border: none !important;
      border-radius: 0;
      padding: 8px 16px;
      font-weight: 400;
      &:hover {
        font-weight: 600;
        background: #F3F4F8;
        color: #000000;
      }
    }
    .login {
      width: 100%;
      justify-content: start !important;
      align-items: start !important;
      padding: 8px 16px;
      &:hover {
        font-weight: 600;
        background: #F3F4F8;
        color: #000000;
      }
    }
    .submenu {
      &--active {
        .submenu-show {
          background: #F3F4F8;
        }
        .submenu-hidden {
          display: block;
          background: #F3F4F8;
        }
      }
      &-show {
        padding: 8px 16px;
      }
      &-hidden {
        position: static;
        transform: unset !important;
        width: 100%;
        display: none;
        padding: 0 0 8px 32px;
        opacity: 1;
        pointer-events: auto;
      }
      &-list {
        gap: 8px;
        background: transparent;
        border: none;
        padding: 0;
        border-radius: unset;
        a {
          padding: 0;
        }
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 8px 0;
    &__right {
      position: fixed;
      z-index: 15;
      top: 0;
      left: 0;
      transform: unset;
      width: 100%;
      max-width: 100%;
      height: 100%;
      border: none;
      border-radius: 0;
      padding: 80px 0 50px 0;
    }
    &__logo {
      width: 30px;
    }
    &__text {
      font-size: 12px;
    }
    .close {
      position: absolute;
      top: 16px;
      right: 16px;
      display: block;
      width: 24px;
      cursor: pointer;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  .burger {
    width: 32px;
    height: 32px;
    span {
      right: 8px;
    }
    span:nth-child(1) {
      top: 10px;
      width: 16px;
    }
    span:nth-child(2) {
      width: 12px;
    }
    span:nth-child(3) {
      bottom: 10px;
      width: 5px;
    }
  }
}

@media screen and (max-width: 400px) {
  .header {
    &__inner {
      gap: 8px;
    }
    &__text {
      font-size: 8px;
    }
  }
}