.btn-blue {
  background: #457EEF;
  border: 1px solid #457EEF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #fff;
  &:hover {
    background: transparent;
    color: #457EEF;
  }
}
.btn-blue__outline {
  background: transparent;
  border: 1px solid #407BFF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #407BFF;
  &:hover {
    background: #407BFF;
    color: #fff;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #000;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #000;
  &:hover {
    background: #000;
    color: #fff;
  }
}
.btn-blue__outline2 {
  background: #fff;
  border: 1px solid #407BFF;
  border-radius: 40px;
  font-size: 20px;
  line-height: 130%;
  font-weight: 700;
  color: #407BFF;
  &:hover {
    background: transparent;
    border-color: #fff;
    color: #fff;
  }
}
.search-form {
  position: relative;
  width: 100%;
  height: 80px;
  background: #FFFFFF;
  border-radius: 40px;
  input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    border-color: transparent;
    border-radius: 40px;
    padding: 0 308px 0 24px;
    font-size: 20px;
    line-height: 130%;
    color: #000;
    &::placeholder {
      color: #ABABAB;
    }
  }
  #placeholderDiv {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 308px 0 24px;
    font-size: 20px;
    line-height: 130%;
    color: #ABABAB;
    pointer-events: none;
    br {
      display: none;
    }
  }
  &__btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 12px;
    transform: translate(0, -50%);
    max-width: 272px;
    width: 100%;
  }
}
.text48 {
  font-size: 48px;
  line-height: 130%;
  font-weight: 700;
}
.text24 {
  font-size: 24px;
  line-height: 130%;
  font-weight: 700;
}
.text16 {
  font-size: 16px;
  line-height: 130%;
}
.swiper-button-next,
.swiper-button-prev {
  width: 40px;
  height: 40px;
  background: #F4F7FF;
  border-radius: 50%;
  &:after {
    font-size: 18px;
    color: #003C61;
  }
}
.swiper-pagination {
  &-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid #ABABAB;
    margin: 0 10px !important;
    opacity: 1;
    &-active {
      background: #ABABAB;
    }
  }
}

@media screen and (max-width: 992px) {
  .btn-blue {
    font-size: 16px;
  }
  .btn-blue__outline {
    font-size: 16px;
  }
  .btn-black__outline {
    font-size: 16px;
  }
  .search-form {
    height: 42px;
    border-radius: 24px;
    &__group {
      input {
        font-size: 16px;
        padding: 0 184px 0 16px;
      }
      #placeholderDiv {
        font-size: 16px;
        padding: 0 184px 0 16px;
      }
    }
    &__btn {
      right: 8px;
      max-width: 160px;
    }
  }
  .text48 {
    font-size: 32px;
  }
}

@media screen and (max-width: 576px) {
  .search-form {
    &__group {
      input {
        font-size: 12px;
        padding: 0 146px 0 8px;
      }
      #placeholderDiv {
        font-size: 12px;
        padding: 0 146px 0 8px;
        br {
          display: block;
        }
      }
    }
  }
  .text48 {
    font-size: 24px;
  }
  .text24 {
    font-size: 16px;
  }
}

@media screen and (max-width: 400px) {
  .search-form {
    &__group {
      input {
        font-size: 9px;
      }
      #placeholderDiv {
        font-size: 9px;
      }
    }
  }
}